
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#device {
  background: #f0eff5;
}
#device > div {
  padding: 10px;
}
#device .status {
  background: #fff;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
#device .status .tit {
  color: #999;
  float: left;
  margin-top: 7px;
}
#device .status .all,
#device .status .off-line,
#device .status .on-line {
  margin-left: 20px;
  border: 2px solid #ddd;
  float: left;
  padding: 5px 20px;
  border-radius: 5px;
}
#device .status .active {
  border: 2px solid #7e8fbd;
  color: #7e8fbd;
}
#device .place {
  background: #fff;
  overflow: hidden;
}
#device .place span {
  float: left;
}
#device .place .tag {
  margin-left: 20px;
  color: #7e8fbd;
  width: 100px;
}
#device .inst {
  overflow: hidden;
}
#device .space-info {
  padding: 0;
}
#device .space-info-t {
  padding: 10px;
  background: #fafafa;
  overflow: hidden;
}
#device .space-info-t .col {
  color: #f00;
}
#device .space-info .list {
  background: #fff;
  padding: 10px;
}
#device .space-info .list li {
  overflow: hidden;
}
#device .space-info .list li .font {
  color: #aaa;
}
#device .space-info .list li .col {
  color: #fff;
  background: #f00;
  border-radius: 8px;
  padding: 5px 8px;
}



